import React, { useEffect, useMemo, useState, useCallback } from "react";
import { navigate, openmrsFetch, restBaseUrl, fhirBaseUrl } from "@openmrs/esm-framework";
import { launchWorkspace  } from "@openmrs/esm-framework";
import { formEntryWorkspace } from "@openmrs/esm-patient-common-lib";



type User = {
  uuid: string;
  username: string;
  roles: { display: string }[];
};

export interface SearchedPatient {
  id: string;
  name?: { text?: string; given?: string[]; family?: string }[];
  identifier?: { system?: string; value?: string; type?: { text?: string } }[];
  gender?: string;
  birthDate?: string;
  meta?: { lastUpdated?: string };
  displayName?: string;
  artId?: string;
  registeredDate?: string;
}

export interface PatientList {
  id: string;
  name: string;
  members: SearchedPatient[];
}

export default function HomePageHeader({ dashboardTitle }: { dashboardTitle: string }) {
  const [user, setUser] = useState<User | null>(null);
  const [patientLists, setPatientLists] = useState<PatientList[]>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const baseUrl = (window.getOpenmrsSpaBase?.() ?? "/openmrs/spa").replace(/\/$/, "");

  useEffect(() => {
    openmrsFetch<{ user: User }>(`${restBaseUrl}/session`)
      .then((res) => setUser(res.data.user))
      .catch((err) => console.error("[HomePageHeader] Error fetching session:", err));
  }, []);

  const userRoles = useMemo(() => {
    return user?.roles?.map((r) => r.display.toLowerCase()) || [];
  }, [user]);

  useEffect(() => {
    const fetchRecentPatients = async () => {
      try {
        const sevenDaysAgo = new Date();
        sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);
        const since = sevenDaysAgo.toISOString();

        const res = await openmrsFetch<{ entry?: { resource: SearchedPatient }[] }>(
          `${fhirBaseUrl}/Patient?_lastUpdated=ge${since}&_sort=-_lastUpdated&_count=50`
        );

        const patients =
          res.data.entry?.map((e) => {
            const patient = e.resource;
            return {
              ...patient,
              artId:
                patient.identifier?.find((id) =>
                  id.type?.text?.toLowerCase().includes("‡§Ü‡§´‡•ç‡§®‡•ã ‡§è‡§Ü‡§∞‡§ü‡§ø  ‡§Ü‡§à‡§°‡•Ä ‡§≤‡•á‡§ñ‡•ç‡§®‡•Å‡§π‡•ã‡§∏‡•ç")
                )?.value || "N/A",
              registeredDate: patient.meta?.lastUpdated || "N/A",
            };
          }) || [];

        setPatientLists([
          {
            id: "recent-patients",
            name: "‡§π‡§æ‡§≤‡•à ‡§¶‡§∞‡•ç‡§§‡§æ ‡§≠‡§è‡§ï‡§æ ‡§∏‡§π‡§≠‡§æ‡§ó‡•Ä‡§π‡§∞‡•Ç",
            members: patients,
          },
        ]);
      } catch (err) {
        console.error("[HomePageHeader] Error fetching recent patients:", err);
      }
    };

    if (userRoles.length && !userRoles.includes("self registration")) {
      fetchRecentPatients();
    }
  }, [userRoles]);


type SearchResult = {
  found: boolean;
  name?: string;
};

  const [identifier, setIdentifier] = useState("");
const [searchResult, setSearchResult] = useState<SearchResult | null>(null);


const searchPatient = async () => {
  if (!identifier) return;

  // OpenMRS exact identifier search
  const res = await fetch(
    `/openmrs/ws/rest/v1/patient?q=${identifier}&v=full`
  );
  const data = await res.json();

  if (data.results.length === 1) {
    setSearchResult({
      found: true,
      name: data.results[0].person.display,
    });
  } else {
    setSearchResult({ found: false });
  }
};


  const addPatient = useCallback(() => {
    navigate({ to: `${baseUrl}/patient-registration` });
  }, [baseUrl]);

const openChart = async (patient: SearchedPatient) => {
  if (!patient.id) return;

  // 1Ô∏è‚É£ Navigate to Patient Chart
  navigate({ to: `${baseUrl}/patient/${patient.id}/chart` });

  // 2Ô∏è‚É£ Give React + Patient Chart time to mount WorkspaceContainer
  await new Promise((resolve) => setTimeout(resolve, 2000));

  // 3Ô∏è‚É£ Launch the ACTUAL registered workspace (NOT a string)
  launchWorkspace(formEntryWorkspace, {
    workspaceTitle: "‡§∏‡§π‡§≠‡§æ‡§ó‡•Ä ‡§´‡§æ‡§∞‡§Æ",
    formInfo: {
      patientUuid: patient.id,
      formUuid: "30413aeb-e448-46c3-b2c1-52e19233906f",
      encounterUuid: undefined,
      visitUuid: undefined,
      visitTypeUuid: undefined,
      visitStartDatetime: undefined,
      visitStopDatetime: undefined,
      htmlForm: null,
    },
  });
};


  // üîç Filter patients locally as user types
  const displayedPatients = useMemo(() => {
    const allPatients = patientLists[0]?.members || [];
    if (!searchTerm.trim()) return allPatients;

    return allPatients.filter((p) =>
      p.artId?.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }, [searchTerm, patientLists]);

 return (
  <>
    {userRoles.includes("self registration") ? (
      <>
        {/* SEARCH SECTION */}
        <div style={{ display: "flex", justifyContent: "center", marginTop: "60px" }}>
          <div style={{ width: "420px", textAlign: "center" }}>
            <input
              type="text"
              placeholder="Enter Exact Identifier"
              value={identifier}
              onChange={(e) => setIdentifier(e.target.value.trim())}
              style={{
                width: "100%",
                padding: "1rem",
                fontSize: "1.2rem",
                borderRadius: "10px",
                border: "2px solid #007f7f",
                marginBottom: "20px",
              }}
            />

            <button
              onClick={searchPatient}
              style={{
                width: "100%",
                background: "#007f7f",
                color: "#fff",
                border: "none",
                borderRadius: "10px",
                padding: "1rem",
                fontSize: "1.2rem",
                fontWeight: "700",
                cursor: "pointer",
              }}
            >
              üîç Search Patient
            </button>

            {/* SEARCH RESULT */}
            {searchResult?.found === true && (
              <div style={{ marginTop: "20px", fontSize: "1.1rem", fontWeight: "600", color: "green" }}>
                ‚úÖ Patient Found: {searchResult.name}
              </div>
            )}

            {searchResult?.found === false && (
              <div style={{ marginTop: "20px", fontSize: "1.1rem", fontWeight: "600", color: "red" }}>
                ‚ùå Patient not registered
              </div>
            )}
          </div>
        </div>

        {/* REGISTER BUTTON */}
        <div style={{ display: "flex", justifyContent: "center", marginTop: "100px" }}>
          <button
            style={{
              background: "linear-gradient(145deg, #005d5d, #007f7f)",
              color: "#fff",
              border: "none",
              borderRadius: "80px",
              padding: "2rem 3.5rem",
              fontSize: "2rem",
              fontWeight: 900,
              fontFamily: "Noto Sans Devanagari, sans-serif",
              cursor: "pointer",
              boxShadow: "0px 6px 12px rgba(0,0,0,0.25)",
              transition: "all 0.3s ease",
            }}
            onMouseEnter={(e) => (e.currentTarget.style.transform = "scale(1.05)")}
            onMouseLeave={(e) => (e.currentTarget.style.transform = "scale(1)")}
            onClick={addPatient}
          >
            ‚úçüèª ‡§¶‡§∞‡•ç‡§§‡§æ ‡§ó‡§∞‡•ç‡§®‡•Å‡§π‡•ã‡§∏‡•ç
          </button>
        </div>
      </>
    ) : (
      <div style={{ marginTop: "80px", marginLeft: "40px", fontFamily: "Noto Sans Devanagari, sans-serif" }}>
        <h3 style={{ color: "#339E71", fontSize: "2.4rem", marginBottom: "25px" }}>
          üßæ ‡§π‡§æ‡§≤‡•à ‡§¶‡§∞‡•ç‡§§‡§æ ‡§≠‡§è‡§ï‡§æ ‡§∏‡§π‡§≠‡§æ‡§ó‡•Ä‡§π‡§∞‡•Ç
        </h3>

        <table
          style={{
            width: "70%",
            borderCollapse: "collapse",
            fontSize: "1.1rem",
            boxShadow: "0 4px 10px rgba(0,0,0,0.15)",
            borderRadius: "12px",
            overflow: "hidden",
          }}
        >
          <thead>
            <tr style={{ backgroundColor: "#005d5d", color: "#fff" }}>
              <th style={{ padding: "14px 18px", textAlign: "left", width: "55%" }}>
                <div style={{ display: "flex", alignItems: "center", gap: "10px" }}>
                  ‡§∏‡§π‡§≠‡§æ‡§ó‡•Ä‡§ï‡•ã ‡§è‡§Ü‡§∞‡§ü‡§ø ‡§Ü‡§à‡§°‡•Ä
                  <input
                    type="text"
                    placeholder="‡§ñ‡•ã‡§ú‡•ç‡§®‡•Å‡§π‡•ã‡§∏‡•ç..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    style={{
                      padding: "6px 10px",
                      fontSize: "0.9rem",
                      borderRadius: "6px",
                      border: "1px solid #ccc",
                      width: "180px",
                    }}
                  />
                </div>
              </th>
              <th style={{ padding: "14px 18px", textAlign: "left" }}>‡§¶‡§∞‡•ç‡§§‡§æ ‡§Æ‡§ø‡§§‡§ø</th>
            </tr>
          </thead>

          <tbody>
            {displayedPatients.length === 0 ? (
              <tr>
                <td colSpan={2} style={{ padding: "12px 18px", fontStyle: "italic" }}>
                  ‡§ï‡•Å‡§®‡•à ‡§∏‡§π‡§≠‡§æ‡§ó‡•Ä ‡§≠‡•á‡§ü‡§ø‡§è‡§®
                </td>
              </tr>
            ) : (
              displayedPatients.map((p, idx) => (
                <tr
                  key={p.id}
                  onClick={() => openChart(p)}
                  style={{
                    cursor: "pointer",
                    backgroundColor: idx % 2 === 0 ? "#f9fdf9" : "#ffffff",
                    transition: "all 0.2s ease",
                  }}
                  onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = "#e6f7f0")}
                  onMouseLeave={(e) =>
                    (e.currentTarget.style.backgroundColor = idx % 2 === 0 ? "#f9fdf9" : "#ffffff")
                  }
                >
                  <td style={{ padding: "12px 18px" }}>{p.artId}</td>
                  <td style={{ padding: "12px 18px" }}>
                    {p.registeredDate ? new Date(p.registeredDate).toLocaleString() : "N/A"}
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    )}
  </>
);

}
